[jin2_demo_list_person_view_list=""] {
    flex: 1;
    overflow: auto;
    position: relative;
    outline: 1px solid rgba( 0 , 0 , 0 , .1 );
}

[jin2_demo_list_person_view_list="rowPerson"] {
    background: rgba( 255 , 255 , 255 , .9 );
    outline: 1px solid rgba( 0 , 0 , 0 , .1 );
}

[jin2_demo_list_person_view_list="rowGroup"] {
    background: rgba( 128 , 128 , 128 , .9 );
    color: white;
    font-weight: bold;
    z-index: 1;
    transition: none;
    padding: 0 .5em;
}
